import React, {useEffect} from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {toogleIsLoading} from "../../../redux/UsersReducer";
import {withRouter} from "react-router-dom";
import {getUserProfileStatus, setUserProfile} from "../../../redux/ProfileReducer";
import withAuthRedirect from "../../../hoc/withAuthRedirect";
import {compose} from "redux";

import {setInitialized} from "../../../redux/appReducer";

let ProfileContainer = (props) => {
    return (
        <Profile/>
    )

}

const mapStateToProps = (state) => {
    return {

    }
}

export default compose(
    withRouter,
    withAuthRedirect,
    connect(mapStateToProps, {toogleIsLoading, setInitialized, setUserProfile, getUserProfileStatus})
)(ProfileContainer);